<template>
<section>

	<div class="count-wrap bluestyle">
		<div class="count-inner">
			<div class="count-text">碳水化合物</div>
			<countTo
				class="count-num" 
				:separator='theseparator' 
				:startVal='startVal' 
				:endVal='endVal' 
				:duration='2000'
			>
			</countTo>
			<div class="count-text">克</div>
		</div>
	</div>
	<div class="result-wrap text-center">
		<div class="result-thumb">
			<img v-if="cover" :src="cover">
		</div>
		<div class="result-text">需补打胰岛素</div>
		<div class="result-time wcar">
			<div class="times" v-if="hour"><span>{{hour}}</span>单位</div>
		</div>
	</div>

	<div class="calculate-link">保存</div>

</section>
</template>

<script>
  import countTo from 'vue-count-to';
  export default {
    components: { countTo },
    data () {
      return {
        startVal: 0,
        endVal: 22.2,
        theseparator:'',
        hour:1,
        min:0,
        cover:''
      }
    },
    head: {
		title: function () {
			return {
				inner: '补打胰岛素',
				separator: ' ', 
        		complement: ' '
			}
		}
	},
    created(){
    	
    },
    methods:{
    	
    }
  }
</script>


