import * as types from './mutation-types'

export default {

  // 新增运动两平衡食物选择列表
  [types.SAVE_PHFOOD] (state, plan) {
    state.phfoodlist.push(plan)
  },
  // 删除运动两平衡食物选择列表
  [types.DELETE_PHFOOD] (state, idx) {
    state.phfoodlist.splice(idx, 1);
  }
};


