<template>
	<section>
		<simplert useRadius=true
		          useIcon=true
		          ref="simplert">
		</simplert>
		<div class="empty" v-if="!foodschosed.length">
			<div class="empty-text text-center">请选择所吃食物</div>
			<div class="text-center">
				<button class="btn-add" @click="poppicker()">
					<span class="iconplus">添加食物</span>
				</button>
			</div>
			<div id="name"></div>
			<div class="bgfoods">
				<img src="../assets/img/foods.png">
			</div>
		</div>
		
		<div class="foods-box" v-if="foodschosed.length">
			<ul class="foods-wrap">
				<li v-for="(foodselected,idx) in foodschosed">
					<span class="food-icon" 
					v-if="foodselected.foodThumb.length"
					:style="{backgroundImage:'url(' + foodselected.foodThumb[0].url + ')'}"></span>
					<div class="food-title ellipsis">{{ foodselected.foodTitle }}</div>
					<div class="food-kilo">{{ foodselected.foodWeight }}g</div>
					<span class="icon-del" @click="delfood(idx)"></span>
				</li>
			</ul>
			<div class="text-center">
				<button class="btn-add" @click="poppicker()">
					<span class="iconplus">添加食物</span>
				</button>
			</div>
		</div>
		
		<!--<router-link to="/chosemotion" class="calculate-link">计算热量</router-link>-->
		<div class="calculate-link" @click="calculate()">查看食物血糖生成指数</div>
	</section>
</template>

<script>
import Picker from 'better-picker'
import Simplert from 'vue2-simplert'

export default {
	components: {Simplert},
    data() {
        return {
          loading:true,
          finished:false,
          foodcats:[],
          foods:[],
          foodschosed:[],
          foodweights:[
				{
					text: '5g',
					value: 5
				}, {
					text: '10g',
					value: 10
				},
				{
					text: '15g',
					value: 15
				}, {
					text: '20g',
					value: 20
				},
				{
					text: '25g',
					value: 25
				}, {
					text: '30g',
					value: 30
				},
				{
					text: '35g',
					value: 35
				}, {
					text: '40g',
					value: 40
				},
				{
					text: '45g',
					value: 45
				}, {
					text: '50g',
					value: 50
				},
				{
					text: '55g',
					value: 55
				}, {
					text: '60g',
					value: 60
				},
				{
					text: '65g',
					value: 65
				}, {
					text: '70g',
					value: 70
				},
				{
					text: '75g',
					value: 75
				}, {
					text: '80g',
					value: 80
				}
			]
        }
    },
    head: {
		title: function () {
			return {
				inner: '热量计算',
				separator: ' ', 
        		complement: ' '
			}
		}
	},
    created(){
    	this.axios.get(apiurl_sugar_foodcats).then((response) => {
			var data1 = []
          	var len = response.data.length
          	for(let i = 0; i<len;i++){
          		data1[i] = {
          			text:response.data[i].name,
          			value:response.data[i].id
          		}
          		// get foods
          		this.axios.get(apiurl_sugar_foods + response.data[i].id).then((responsefood) => {
					var foodsresult = responsefood.data
					var foods_len = responsefood.data.length
					var foods_arr = []
					for(var j = 0; j<foods_len; j++){
						foods_arr[j] = {
							text:foodsresult[j].title.rendered,
							value:foodsresult[j].id,
							foodthumb:foodsresult[j].meta_box.index_food_image,
							food_useable_percent:foodsresult[j].meta_box.index_food_index
						}
					}
					this.foods[i] = foods_arr
				})
          		// end get foods
          	}
          	this.foodcats = data1
		})

    },
    methods:{
    	poppicker(){
    		var interv;
		    interv = setInterval(() => {
		      if(this.foods.length > 0){
		      	clearInterval(interv)
		      	//
		      	var data1 = this.foodcats
		      	var data2 = this.foods[0]
		      	var data3 = this.foodweights
		      	var picker = new Picker({
					data: [data1, data2, data3],
					selectedIndex: [0, 1, 2],
					title: '选择食物'
				});
				var _that = this
				picker.on('picker.change', function (index, selectedIndex) {
					if(index == 0){
						var second = _that.foods[selectedIndex]
						picker.refillColumn(1, second);
					}

				});
				picker.on('picker.select', function (selectedVal, selectedIndex) {
					data2 = _that.foods[selectedIndex[0]]
					var temp_arr = {
						foodTitle: data2[selectedIndex[1]].text,
						foodWeight: data3[selectedIndex[2]].value,
						foodThumb: data2[selectedIndex[1]].foodthumb,
						foodUseable: data2[selectedIndex[1]].food_useable_percent
					}
					_that.foodschosed.push(temp_arr)

				})
				picker.on('picker.valuechange', function (selectedVal, selectedIndex) {
				});
				picker.show();
		      	//
		      }
		    }, 50)
    		///
    	},
    	delfood(idx){
    		this.foodschosed.splice(idx,1)
    	},
    	calculate(){
    		
    		if(!this.foodschosed.length){
    			//
    			let obj = {
				    message: '请选择所吃食物',
				    type: 'error',
				    customCloseBtnText:'确定'
				}
				this.$refs.simplert.openSimplert(obj)
    			///
    		}else{
    			this.$router.push({ path: '/gis', query: { foodlist:this.foodschosed}});
    		}
    	}
    }
}
</script>


