<template>
<section>
	<simplert useRadius=true
	          useIcon=true
	          ref="simplert">
	</simplert>
	<transition name="zoomIn">
		<div class="poper" v-show="show">
			<div class="inner">
				<i @click="hidepop()"></i>
				<div class="title">请输入餐前血糖值</div>
				<div class="input">
					<input type="text" id="typevalue" v-model="data_canqian">
				</div>
				<button @click="savedata()">保存</button>
			</div>
		</div>
	</transition>
	<transition name="zoomIn">
		<div class="poper" v-show="show1">
			<div class="inner">
				<i @click="hidepop1()"></i>
				<div class="title">请输入餐后目标值</div>
				<div class="input">
					<input type="text" id="typevalue1" v-model="data_canhou">
				</div>
				<button @click="savedata1()">保存</button>
			</div>
		</div>
	</transition>
	<transition name="zoomIn">
		<div class="poper" v-show="show2">
			<div class="inner">
				<i @click="hidepop2()"></i>
				<div class="title">请输入一日胰岛素总用量</div>
				<div class="input">
					<input type="text" id="typevalue2" v-model="data_total">
				</div>
				<button @click="savedata2()">保存</button>
			</div>
		</div>
	</transition>
	<transition name="zoomIn">
		<div class="poper" v-show="show3">
			<div class="inner">
				<i @click="hidepop3()"></i>
				<div class="title">请输入使用胰岛素类别</div>
				<div class="input">
					<input type="text" id="typevalue3" v-model="data_zhushe">
				</div>
				<button @click="savedata3()">保存</button>
			</div>
		</div>
	</transition>
		
	<div class="count-wrap bluestyle">
		<div class="count-inner">
			<div class="count-text">碳水化合物</div>
			<countTo
				class="count-num" 
				:separator='theseparator' 
				:startVal='startVal' 
				:endVal='endVal' 
				:duration='2000'
			>
			</countTo>
			<div class="count-text">克</div>
		</div>
	</div>
	
	<div class="chose-wrap">
		<ul>
			<li @click="showpop()">
				<div class="icon-canqian">餐前血糖值</div>
				<div class="icon-arrow-right">
					<span>{{data_canqian}}</span>
				</div>
			</li>
			<li @click="showpop1()">
				<div class="icon-canhou">餐后目标值</div>
				<div class="icon-arrow-right motion">
					{{data_canhou}}
				</div>
			</li>
			<li @click="showpop2()">
				<div class="icon-total">一日胰岛素总用量</div>
				<div class="icon-arrow-right">
					<span>{{data_total}}</span>
				</div>
			</li>
			<li @click="showpop3()">
				<div class="icon-zhushe">使用胰岛素类别</div>
				<div class="icon-arrow-right">
					{{data_zhushe}}
				</div>
			</li>
		</ul>
	</div>

	<div class="calculate-link" @click="goresult()">计算餐前注射胰岛素</div>

</section>
</template>

<script>
  import countTo from 'vue-count-to';
  import Simplert from 'vue2-simplert'
  export default {
    components: { countTo,Simplert },
    data () {
      return {
        startVal: 0,
        endVal: 22.2,
        theseparator:'',
        hour:1,
        min:0,
        cover:'',
        show:false,
        show1:false,
        show2:false,
        show3:false,
        data_canqian:'',
        data_canhou:'',
        data_total:'',
        data_zhushe:''
      }
    },
    head: {
        title: function () {
            return {
                inner: '餐前注射胰岛素',
                separator: ' ', 
                complement: ' '
            }
        }
    },
    created(){
    	
    },
    methods:{
    	hidepop(){
    		this.show = false
    	},
    	showpop(){
    		this.show = true
    		this.$nextTick(function () {
		        document.getElementById("typevalue").focus()
		    })
    	},
    	hidepop1(){
    		this.show1 = false
    	},
    	showpop1(){
    		this.show1 = true
    		this.$nextTick(function () {
		        document.getElementById("typevalue1").focus()
		    })
    	},
    	hidepop2(){
    		this.show2 = false
    	},
    	showpop2(){
    		this.show2 = true
    		this.$nextTick(function () {
		        document.getElementById("typevalue2").focus()
		    })
    	},
    	hidepop3(){
    		this.show3 = false
    	},
    	showpop3(){
    		this.show3 = true
    		this.$nextTick(function () {
		        document.getElementById("typevalue3").focus()
		    })
    	},
    	savedata(){
    		this.show = false
    	},
    	savedata1(){
    		this.show1 = false
    	},
    	savedata2(){
    		this.show2 = false
    	},
    	savedata3(){
    		this.show3 = false
    	},
    	goresult(){
    		if(!this.data_canqian){
                let obj = {
                    message: '请填写餐前血糖值',
                    type: 'error',
                    customCloseBtnText:'确定'
                }
                this.$refs.simplert.openSimplert(obj)
                return false
            }
            if(!this.data_canhou){
                let obj = {
                    message: '请填写餐后目标值',
                    type: 'error',
                    customCloseBtnText:'确定'
                }
                this.$refs.simplert.openSimplert(obj)
                return false
            }
            if(!this.data_total){
                let obj = {
                    message: '请填写一日胰岛素总用量',
                    type: 'error',
                    customCloseBtnText:'确定'
                }
                this.$refs.simplert.openSimplert(obj)
                return false
            }
            if(!this.data_zhushe){
                let obj = {
                    message: '请填写使用胰岛素类别',
                    type: 'error',
                    customCloseBtnText:'确定'
                }
                this.$refs.simplert.openSimplert(obj)
                return false
            }
            this.$router.replace({ path: '/carbofinal'});
    	}
    }
  }
</script>

<style>
.zoomIn-enter-active {
  animation-name: zoomIn;
  animation-duration: .2s;
}
.zoomIn-leave-active {
  animation-name: zoomOut;
  animation-duration: .2s;
}
</style>


